<template>
  <v-container>
    <div>
      <v-card tile class="mx-auto">
        <v-img
          position="bottom"
          class="white--text"
          height="70px"
          src="@/assets/card-bg.jpg"
        >
          <v-card-text class="text-right text-h6 font-weight-black">
            INSURER LIST
          </v-card-text>
        </v-img>
      </v-card>

      <v-row class="mt-2">
        <v-col cols="12" md="12">
          <v-card>
            <div class="pl-5 pr-5">
              <v-row>
                <v-col cols="12" sm="12">
                  <v-card-title>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          color="primary"
                          to="/addIns"
                          class="text-decoration-none"
                          dark
                          fab
                          v-bind="attrs"
                          v-on="on"
                          small
                        >
                          <v-icon>mdi-plus</v-icon>
                        </v-btn>
                      </template>
                      <span>ADD INSURER</span>
                    </v-tooltip>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="search"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-card-title>

                  <v-data-table
                    :headers="headers"
                    :items="inss"
                    :search="search"
                    :hide-default-footer="false"
                  >
                    <template slot="item.numbering" slot-scope="props">
                      {{ props.index + 1 }}
                    </template>

                    <template v-slot:[`item.actions`]="{ item }">
                      <v-chip
                        dark
                        small
                        color="teal lighten-2"
                        @click="editIns(item.id)"
                        class="mr-1"
                      >
                        <span>EDIT</span>
                      </v-chip>
                      <v-chip
                        dark
                        small
                        color="red darken-1"
                        @click="deleteIns(item.id)"
                      >
                        <span>DELETE</span>
                      </v-chip>
                    </template>
                  </v-data-table>
                </v-col>
              </v-row>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import InsDataService from "../../services/InsDataService";
export default {
  name: "ins-list",
  data() {
    return {
      inss: [],
      search: "",
      headers: [
        {
          text: "NO.",
          align: "start",
          sortable: false,
          value: "numbering",
        },
        {
          text: "NAME",
          align: "start",
          sortable: false,
          value: "name",
          // class: "blue lighten-5",
        },
        {
          text: "CODE",
          align: "center",
          value: "insCode",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "ACTIONS",
          align: "center",
          value: "actions",
          sortable: false,
          // class: "blue lighten-5",
        },
      ],
    };
  },

  computed: {},

  methods: {
    retrieveInss() {
      InsDataService.getAll()
        .then((response) => {
          this.inss = response.data.map(this.getDisplayIns);
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveInss();
    },

    removeAllInss() {
      InsDataService.deleteAll()
        .then((response) => {
          console.log(response.data);
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    editIns(id) {
      this.$router.push({ name: "insurer", params: { id: id } });
    },

    deleteIns(id) {
      InsDataService.delete(id)
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getDisplayIns(insurer) {
      return {
        id: insurer.id,
        name:
          insurer.name.length > 30
            ? insurer.name.substr(0, 30) + "..."
            : insurer.name,
        insCode:
          insurer.insCode.length > 30
            ? insurer.insCode.substr(0, 30) + "..."
            : insurer.insCode,
      };
    },
  },
  mounted() {
    this.retrieveInss();
  },
};
</script>
